.icon {
  display: block;
  height: 32px;
  width: 32px;

  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

  @include hover(transform, scale(1.05));

  &_menu {
    background-image: url(/src/images/icons/icon_burger_menu.svg);

    @include on-desktop {
      display: none;
    }
  }

  &_cross {
    background-image: url(/src/images/icons/icon_close.svg);
  }
}
